.app .about {
  margin: 6% 2% 10% 2%;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 40px;
  font-size: 1em;
  overflow: hidden; }
  .app .about .about-heading {
    margin: 0;
    padding: 0;
    font-size: 1.5em;
    grid-column: span 4;
    grid-row: span 1; }
  .app .about .about-paragraph {
    grid-row: span 1;
    grid-column: 1 / span 4;
    margin: 0;
    text-align: justify;
    word-break: break-word; }
  .app .about .about-images {
    margin: 0 auto;
    grid-column: 5 / span 2;
    grid-row: 2 / span 3;
    border: 6px solid #000;
    padding: 5px;
    box-sizing: border-box;
    width: 100%;
    max-width: 500px; }
